import { SceneBase } from '../core/sceneBase'
import * as PuzzleProduct from '@/activity/core/resource/product/puzzleProduct'
import config from '../gameUtil/config'
import { ResourceManager } from '../core/resourceManager'
import gsap from 'gsap'
import { Dimmed } from '../widget/dimmed'
import { LoadingScreen } from '../scene/loading'
import { PuzzleResource } from '../core/resource/viewer/puzzle'
import { EventSprite } from '../widget/eventSprite'
import { debugLine } from '../gameUtil/game'
import { App } from '../core/app'

const pieceWidth = 250
const pieceHeight = 230
const x = pieceWidth / 2 + 20
const y = pieceHeight / 2 + 20

const piecePos = [
  { x: x, y: y },
  { x: x + pieceWidth, y: y },
  { x: x + pieceWidth * 2, y: y },
  { x: x, y: y + pieceHeight },
  { x: x + pieceWidth, y: y + pieceHeight },
  { x: x + pieceWidth * 2, y: y + pieceHeight },
  { x: x, y: y + pieceHeight * 2 },
  { x: x + pieceWidth, y: y + pieceHeight * 2 },
  { x: x + pieceWidth * 2, y: y + pieceHeight * 2 }
]

const puzzleState = [
  { state: false },
  { state: false },
  { state: false },
  { state: false },
  { state: false },
  { state: false },
  { state: false },
  { state: false },
  { state: false }
]
export class Piece extends PIXI.Sprite {
  private mDragFlag: boolean
  private mIndex: number
  get index(): number {
    return this.mIndex
  }
  constructor(index: number, resource: PIXI.BaseTexture, drag?: boolean) {
    super()

    let xI = index
    let yI = 0

    if (index >= 3) {
      xI = index - 3
      yI = 1
    }
    if (index >= 6) {
      xI = index - 6
      yI = 2
    }

    const SlicedCard = new PIXI.Texture(
      resource,
      new PIXI.Rectangle(250 * xI, 230 * yI, 250, 230)
    )
    this.texture = SlicedCard
    this.anchor.set(0.5)
    this.mIndex = index

    const guideLine = new PIXI.Graphics()
    guideLine.lineStyle(6, 0x000000, 1)
    guideLine.drawRect(0, 0, this.width, this.height)
    guideLine.endFill()
    guideLine.pivot.set(guideLine.width / 2, guideLine.height / 2)
    this.addChild(guideLine)
    if (drag) {
      this.mDragFlag = false
      this.registEvent()
    }
  }
  registEvent() {
    this.interactive = true
    this.buttonMode = true
    this.on('pointerdown', () => {
      this.mDragFlag = true
      this.onPointerDown()
    })
    this.on('pointermove', (evt: PIXI.InteractionEvent) => {
      if (this.mDragFlag == false) {
        return
      }
      this.onPointerMove(evt)
    })
    this.on('pointerup', (evt: PIXI.InteractionEvent) => {
      if (this.mDragFlag) {
        this.onPointerUp(evt)
      }
      this.mDragFlag = false
    })
    this.on('pointerout', (evt: PIXI.InteractionEvent) => {
      if (this.mDragFlag) {
        this.onPointerUp(evt)
      }
      this.mDragFlag = false
    })
    this.on('pointerupoutside', (evt: PIXI.InteractionEvent) => {
      if (this.mDragFlag) {
        this.onPointerUp(evt)
      }
      this.mDragFlag = false
    })
  }
  onPointerDown() {
    /** */
  }
  onPointerMove(evt: PIXI.InteractionEvent) {
    /** */
  }
  onPointerUp(evt?: PIXI.InteractionEvent) {
    /** */
  }
}

export class IntroScene extends PIXI.Container {
  constructor() {
    super()
    const bg = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource('bg_1.png').texture
    )
    this.addChild(bg)
  }
  async onInit() {
    const close = new EventSprite(
      ResourceManager.Handle.getViewerResource('button.png').texture
    )
    close.position.set(1220, 80)
    close.interactive = true
    this.addChild(close)
    close.on('pointertap', () => {
      gsap
        .to(close.scale, { x: 1.2, y: 1.2, duration: 0.5 })
        .yoyo(true)
        .repeat(1)
        .eventCallback('onComplete', () => {
          location.href = 'https://www.imestudy.co.kr:443'
        })
    })

    this.createText()
    this.createMuck()
  }

  createText() {
    //   
    const title = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource('title_text.png').texture
    )
    title.position.set(config.w / 2 - title.width / 2, 40)

    //    
    const titleText = new PIXI.Text(` `, {
      fill: 0xffffff,
      fontSize: 35,
      fontFamily: 'TmoneyRoundWindExtraBold'
    })
    titleText.pivot.set(titleText.width / 2, titleText.height / 2)
    titleText.position.set(277 - titleText.width / 2 - 32, title.height / 2)

    //    
    const subText = new PIXI.Text(
      ` !     .`,
      {
        fill: 0xffb612,
        fontSize: 31,
        fontFamily: 'TmoneyRoundWindRegular'
      }
    )
    subText.pivot.set(subText.width / 2, subText.height / 2)
    subText.position.set(599, title.height / 2)

    title.addChild(titleText, subText)
    this.addChild(title)
  }

  createMuck() {
    const muck = new EventSprite(
      ResourceManager.Handle.getProductResource(
        `day${config.studyDay}_muck.png`
      ).texture
    )
    muck.position.set(config.w / 2, 0)

    gsap
      .to(muck, { y: 432, duration: 0.5, ease: 'bounce' })
      .eventCallback('onComplete', async () => {
        muck.texture = ResourceManager.Handle.getProductResource(
          `day${config.studyDay}_muck_intro.png`
        ).texture

        await this.speakMuck()
      })

    this.addChild(muck)
  }

  speakMuck(): Promise<void> {
    return new Promise<void>((resolve) => {
      const speak = new PIXI.Sprite(
        ResourceManager.Handle.getViewerResource('text.png').texture
      )
      speak.anchor.set(0.5)
      speak.position.set(980, 240)

      console.log(`%c    `, 'border:2px red solid;')
      const text = new PIXI.Text(`?`, {
        fill: 0x00aaec,
        fontFamily: 'TmoneyRoundWindExtraBold',
        fontSize: 40
      })
      text.pivot.set(text.width / 2, text.height / 2)
      text.position.set(0, -10)

      speak.addChild(text)

      this.addChild(speak)

      speak.scale.set(0)
      gsap
        .to(speak.scale, { x: 1, y: 1, duration: 0.5, ease: 'back' })
        .eventCallback('onComplete', () => {
          resolve()
        })
    })
  }
}

export class Puzzle extends SceneBase {
  private mIntroScene: IntroScene
  private mGameScene: PIXI.Container

  private mPieceAry: Array<Piece>
  private mDisableMotion: any
  private mBackUpPos: Array<number>

  constructor() {
    super('puzzle')
  }
  async onInit() {
    App.Handle.modalRoot.visible = false

    await App.Handle.showLoadingScreen()

    console.log(this.gamename)
    this.mPieceAry = []
    await this.loadViewerResource(PuzzleResource)
    await this.loadProductResource(PuzzleProduct[`day${config.studyDay}`])

    await App.Handle.closeLoadingScreen()
  }
  async onStart() {
    this.mBackUpPos = []

    const bg = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource('bg_2.png').texture
    )
    this.addChild(bg)

    this.mIntroScene = new IntroScene()
    this.addChild(this.mIntroScene)
    await this.mIntroScene.onInit()
  }
  createPuzzlePlace() {
    for (let i = 0; i < 9; i++) {
      const piece = new Piece(
        i,
        ResourceManager.Handle.getProductResource(
          `day${config.studyDay}_muck.png`
        ).texture.baseTexture
      )
      this.addChild(piece)
      piece.position.set(piecePos[i].x, piecePos[i].y)

      gsap.to(piece, { alpha: 0.1, duration: 0.5 })
    }
  }
  createPiece() {
    for (let i = 0; i < 9; i++) {
      /**  */
      // const piece = new Piece(
      //   i,
      //   ResourceManager.Handle.getProductResource(
      //     `day${config.studyDay}_piece${i + 1}.png`
      //   ).texture,
      //   true
      // )
      const piece = new Piece(
        i,
        ResourceManager.Handle.getProductResource(
          `day${config.studyDay}_muck.png`
        ).texture.baseTexture,
        true
      )

      this.addChild(piece)
      this.mPieceAry.push(piece)
      piece.position.set(900, config.h / 2 - 100)
      const randomX = Math.floor(Math.random() * 3) * 100
      const randomY = Math.floor(Math.random() * 3) * 100
      gsap.to(piece, {
        x: piece.x + randomX,
        y: piece.y + randomY,
        duration: 0.5
      })

      this.sortableChildren = true
      piece.zIndex = 9 - i

      /**  */

      /** 
       * pointerdown
       *     flag true 
       * pointermove
       *    flag true    =>this.check()  
       * pointerup
       *    flag flase      
       */

      piece.onPointerDown = () => {
        for (const pc of this.mPieceAry) {
          pc.zIndex = 1
        }
        piece.zIndex = 2
        this.mBackUpPos = [piece.x, piece.y]
        //
      }
      piece.onPointerMove = (evt: PIXI.InteractionEvent) => {
        piece.position.set(evt.data.global.x, evt.data.global.y)[
          (piece.x, piece.y)
        ]
        this.check(piece)
      }
      piece.onPointerUp = (evt: PIXI.InteractionEvent) => {
        if (!evt || evt.data.global.x < 800) {
          gsap.to(piece, {
            x: this.mBackUpPos[0],
            y: this.mBackUpPos[1],
            duration: 0.2,
            ease: 'back'
          })
        }
      }
    }
  }
  check(piece: Piece) {
    const x = Math.abs(piecePos[piece.index].x - piece.x)
    const y = Math.abs(piecePos[piece.index].y - piece.y)
    const length = Math.pow(x, 2) + Math.pow(y, 2)

    if (length < Math.pow(250, 2)) {
      piece.onPointerUp()
      piece.onPointerDown = () => {
        this.disableMotion(piece)
      }
      piece.onPointerMove = () => null
      piece.onPointerUp = () => null

      puzzleState[piece.index].state = true

      gsap
        .to(piece, {
          x: piecePos[piece.index].x,
          y: piecePos[piece.index].y,
          duration: 0.2,
          ease: 'back'
        })
        .eventCallback('onComplete', () => {
          this.endGame()
        })
    }
  }

  endGame() {
    for (const state of puzzleState) {
      if (state.state == false) {
        return
      }
    }
    console.log(``)
    const dimmed = new Dimmed(config.w, config.h)
    dimmed.alpha = 0
    dimmed.zIndex = 20
    this.addChild(dimmed)
    gsap.to(dimmed, { alpha: 1, duration: 0.5 })
    const text = new PIXI.Text(``, { fontSize: 100, fill: 0xffffff })
    text.pivot.set(text.width / 2, text.height / 2)
    text.position.set(config.w / 2, config.h / 2)
    dimmed.addChild(text)
    text.interactive = true
    text.on('pointertap', () => {
      window.location.reload()
    })
  }

  disableMotion(piece: Piece) {
    if (this.mDisableMotion) {
      this.mDisableMotion.kill()
      piece.angle = 0
    }
    this.mDisableMotion = gsap.timeline({ yoyo: true })
    this.mDisableMotion.to(piece, { angle: -15, duration: 0.1 })
    this.mDisableMotion.to(piece, { angle: 15, duration: 0.1 })
    this.mDisableMotion.to(piece, { angle: 0, duration: 0.1 })
  }
}
