import Config from '@/activity/gameUtil/config'
import gsap from 'gsap'
import { App } from '@/activity/core/app'
import { ResourceManager } from '@/activity/core/resourceManager'
import { EventSprite } from '../widget/eventSprite'
import config from '@/activity/gameUtil/config'
import { soundPlay } from '../gameUtil/game'

const list = [
  'today',
  'rhythm',
  'paint',
  'partner',
  'myhangul',
  'nodak',
  'total',
  'puzzle'
]

const currentX = [20, 176, 330, 488, 646]

export class Progress extends PIXI.Sprite {
  private mName: string
  private mIndex: number
  private mCurrent: EventSprite
  constructor(gameName: string) {
    super()
    this.mName = gameName
    this.texture = ResourceManager.Handle.getCommonResource(
      `outro_stage.png`
    ).texture

    console.log(this.mName)
    this.mIndex = 0

    for (const index of list) {
      if (this.mName == index) {
        this.mIndex = list.indexOf(index)
      }
    }
    this.mCurrent = new EventSprite(
      ResourceManager.Handle.getCommonResource(`outro_check.png`).texture
    )
    this.addChild(this.mCurrent)
    this.mCurrent.position.set(currentX[this.mIndex], this.mCurrent.height / 2)
    gsap
      .to(this.mCurrent.scale, { x: 1.4, y: 1.4, duration: 0.56 })
      .repeat(-1)
      .yoyo(true)
  }
}

export class Eop extends PIXI.Container {
  private mDimmde: PIXI.Graphics
  private mProcessBar: PIXI.Sprite
  private mSpine: PIXI.spine.Spine

  constructor() {
    super()
  }

  createBg() {
    this.mDimmde = new PIXI.Graphics()
    this.mDimmde.beginFill(0x000000, 1)
    this.mDimmde.drawRect(0, 0, Config.w, Config.h)
    this.mDimmde.endFill()
    this.addChild(this.mDimmde)
    this.mDimmde.alpha = 0

    this.mDimmde.interactive = true

    this.mProcessBar = new Progress(App.Handle.getSceneName())
    this.mProcessBar.position.set(config.w / 2 - this.mProcessBar.width / 2, 76)
    this.addChild(this.mProcessBar)

    this.mSpine = new PIXI.spine.Spine(
      ResourceManager.Handle.getCommonResource('outro.json').spineData
    )
  }

  start(): Promise<void> {
    return new Promise<void>((resolve) => {
      this.createBg()

      const btn = new EventSprite(
        ResourceManager.Handle.getCommonResource(`outro_next_btn.png`).texture
      )
      btn.position.set(config.w / 2, config.h - btn.height / 2 - 96)
      btn.scale.set(0)
      this.addChild(btn)

      // soundPlay(`com_eop_sfx`, true)

      gsap
        .to(this.mDimmde, { alpha: 0.8, duration: 0.5 })
        .eventCallback('onComplete', () => {
          this.mSpine.position.set(config.w / 2, config.h / 2)
          this.mSpine.stateData.defaultMix = 0.25
          this.addChild(this.mSpine)

          const random = Math.ceil(Math.random() * 4)
          const snd = `com_eop_sound${random}`
          console.log(`${random} eop  `)

          soundPlay(snd, true)

          this.mSpine.state.setAnimation(0, '01', false)
          const delay = this.mSpine.state.data.skeletonData.findAnimation('01')
            .duration
          gsap.delayedCall(delay, () => {
            if (this.mSpine) this.mSpine.state.setAnimation(0, '02', true)
            gsap.to(btn.scale, { x: 1, y: 1, duration: 0.5, ease: 'back' })
          })

          btn.on(`pointertap`, () => {
            this.mSpine.destroy()
            this.mSpine = null
            this.removeChildren()
            resolve()
          })
        })
    })
  }
}
