export class BgCharacter extends PIXI.Container {
  private mIndex: number
  private mCloud: PIXI.Sprite
  private mCharacter: PIXI.Sprite
  private mCloudMotion: any
  private mCharacterMotion: any
  constructor(index: number) {
    super()
    this.mIndex = index

    this.mCloud = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource(`cloud0${index + 1}.png`).texture
    )
    this.mCharacter = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource(`bg_ch${index + 1}.png`).texture
    )
    this.mCloud.position.set(60, this.mCharacter.height / 2)

    this.addChild(this.mCloud, this.mCharacter)
    this.mCloudMotion = gsap
      .to(this.mCloud, { y: 10, duration: 5 })
      .yoyo(true)
      .repeat(-1)
    // this.mCharacterMotion = gsap
    //   .to(this.mCharacter, { x: 10, duration: 5 })
    //   .yoyo(true)
    //   .repeat(-1)
  }
  async move(yValue: number) {
    gsap
      .to(this, { y: yValue - 10, duration: 2, ease: Power0.easeNone })
      .repeat(-1)
      .yoyo(true)
  }

  destroy() {
    this.move = () => null
    this.mCharacterMotion.kill()
    this.mCharacterMotion = null
    this.mCloudMotion.kill()
    this.mCloudMotion = null
  }
}

export class TextSprite extends PIXI.Sprite {
  private mBlinkMotion: any
  constructor(texture: string) {
    super()
    console.log(texture)
    this.texture = ResourceManager.Handle.getCommonResource(texture).texture
    this.tint = 0x000000
  }
  blink(flag: boolean) {
    if (this.mBlinkMotion) {
      this.mBlinkMotion.kill()
      this.mBlinkMotion = null
    }
    if (flag == false) {
      this.blink = () => null
      return
    }
    this.mBlinkMotion = gsap.timeline({})
    this.mBlinkMotion.to(this, { alpha: 0, duration: 0.25 })
    this.mBlinkMotion.to(this, { tint: 0xffffff, duration: 0 })
    this.mBlinkMotion.to(this, { alpha: 1, duration: 0.25 })
    this.mBlinkMotion.to(this, { alpha: 0, duration: 0.25 })
    this.mBlinkMotion.to(this, { tint: 0x000000, duration: 0 })
    this.mBlinkMotion.to(this, { alpha: 1, duration: 0.25 })
    this.mBlinkMotion.eventCallback('onComplete', () => {
      gsap.delayedCall(3, () => {
        this.blink(true)
      })
    })
  }
}

export class SlicedSyllable extends PIXI.Container {
  private mInfo: object
  private mBg: PIXI.Sprite

  private mDimmed: EventSprite

  private mGameStep: number
  get totalStep(): number {
    return Object.keys(this.mInfo).length
  }
  set gameStep(v: number) {
    this.completeFocus()
    this.mGameStep += 1
  }

  private mQuizSound: Array<HTMLAudioElement>
  private mWordSound: HTMLAudioElement
  get firstSound(): HTMLAudioElement {
    return this.mQuizSound[0]
  }

  private mDeepMotion: any
  private mFocusMotion: any

  private mDeepAry: Array<PIXI.Sprite>
  private mFocusAry: Array<PIXI.Sprite>
  private mDefaultAry: Array<PIXI.Sprite>
  private mPinkAry: Array<PIXI.Sprite>
  private mTextAry: Array<TextSprite>

  private mCorrect: TextSprite
  get correct(): TextSprite {
    return this.mCorrect
  }

  set introVersion(v: boolean) {
    if (v == true) {
      this.focus = () => null
      this.onClick = () => null

      this.removeChild(this.mDimmed)
      this.mDimmed = null

      for (const deep of this.mDeepAry) {
        deep.alpha = 1
      }
      for (const pink of this.mPinkAry) {
        pink.alpha = 1
      }
      for (const text of this.mTextAry) {
        text.alpha = 1
      }
    }
  }
  constructor(info: object) {
    super()
    this.mGameStep = 0
    this.mQuizSound = []
    /**
     * ex
     *    
     *  this.mInfo =
     *      0: {
                type: 1
                text: ""
     *          index: 0
                syllableIndex: 0
                syllableType: "1cho_t1"
              },
     *      1: {
                type: 1
                text: ""
                index: 1
                syllableIndex: 1
                syllableType: "2jung_t1"
              }
     */
    console.log(info)
    this.mInfo = info
    this.mBg = new PIXI.Sprite(
      ResourceManager.Handle.getCommonResource('box_shadow.png').texture
    )
    this.addChild(this.mBg)

    this.mFocusAry = []
    this.mDeepAry = []
    this.mDefaultAry = []
    this.mPinkAry = []
    this.mTextAry = []
    this.createElement()
    this.createText()
    this.pivot.set(this.width / 2, this.height / 2)

    this.interactive = true
    this.buttonMode = true
    this.on('pointertap', () => {
      this.onClick()
    })
  }
  onClick() {
    if (this.mQuizSound[this.mGameStep]) {
      if (this.mDimmed !== null) {
        return
      }
      this.interactive = false
      this.mQuizSound[this.mGameStep].play()
      gsap.delayedCall(this.mQuizSound[this.mGameStep].duration, () => {
        this.interactive = true
      })
    }
  }
  createElement() {
    this.mDimmed = new EventSprite(
      ResourceManager.Handle.getCommonResource(`box_shadow.png`).texture
    )
    this.mDimmed.tint = 0xbcbcbc
    this.mDimmed.alpha = 0.5
    this.mDimmed.onClick = () => {
      console.log(` `)
    }
    // this.mDimmed.tint = 0x000000

    this.sortableChildren = true
    for (let i = 0; i < Object.keys(this.mInfo).length; i++) {
      const deep = new PIXI.Sprite(
        ResourceManager.Handle.getCommonResource(
          `type${this.mInfo[i].type}_${i + 1}deep.png`
        ).texture
      )
      const defaultSliced = new PIXI.Sprite(
        ResourceManager.Handle.getCommonResource(
          `type${this.mInfo[i].type}_${i + 1}default.png`
        ).texture
      )
      const pink = new PIXI.Sprite(
        ResourceManager.Handle.getCommonResource(
          `type${this.mInfo[i].type}_${i + 1}pink.png`
        ).texture
      )
      const focus = new PIXI.Sprite(
        ResourceManager.Handle.getCommonResource(
          `type${this.mInfo[i].type}_focus${i + 1}.png`
        ).texture
      )

      defaultSliced.zIndex = 0
      deep.zIndex = 1
      pink.zIndex = 2
      focus.zIndex = 3
      this.mDimmed.zIndex = 4
      // focus.tint = 0x71b7db

      deep.alpha = 0
      pink.alpha = 0
      focus.alpha = 0

      this.mDefaultAry.push(defaultSliced)
      this.mDeepAry.push(deep)
      this.mPinkAry.push(pink)
      this.mFocusAry.push(focus)
      this.addChild(deep, defaultSliced, pink, focus, this.mDimmed)
    }
  }

  //    
  createText() {
    /**
     * ex=> ''
     * this.mInfo =
     * 0: {type: 1, text: "", index: 0}
     * 1: {type: 1, text: "", index: 0}
     */

    this.mWordSound = document.createElement('audio')
    this.mWordSound.src = `${config.restAPI}common/sounds/word_${config.studyDay}.mp3`

    for (let i = 0; i < Object.keys(this.mInfo).length; i++) {
      let text = null
      const sound = document.createElement('audio')
      if (i == 0) {
        text = new TextSprite(
          `1cho_t${this.mInfo[i].type}_${syllableToNum(this.mInfo[i].text)}.png`
        )
        sound.src = `${config.restAPI}common/sounds/1cho_18_t1.mp3`
      }
      if (i == 1) {
        text = new TextSprite(
          `2jung_t${this.mInfo[i].type}_${syllableToNum(
            this.mInfo[i].text
          )}.png`
        )
        sound.src = `${config.restAPI}common/sounds/2jung_1.mp3`
      }
      if (i == 2) {
        text = new TextSprite(
          `3jong_at_${syllableToNum(this.mInfo[i].text)}.png`
        )
        sound.src = `${config.restAPI}common/sounds/3jong_21.mp3`
      }
      this.addChild(text)
      text.tint = 0x000000
      text.zIndex = 4
      text.alpha = 0
      this.mTextAry.push(text)

      this.mQuizSound.push(sound)
    }
  }

  focus() {
    this.removeChild(this.mDimmed)
    this.mDimmed = null
    // if (this.mDeepMotion) {
    //   this.mDeepMotion.kill()
    //   this.mDeepMotion = null
    // }
    if (this.mFocusMotion) {
      this.mFocusMotion.kill()
      this.mFocusMotion = null
    }
    if (!this.mFocusAry[this.mGameStep]) {
      return
    }
    this.mFocusMotion = gsap.timeline({ repeat: -1, yoyo: true })
    this.mFocusMotion.to(this.mFocusAry[this.mGameStep], {
      alpha: 1,
      duration: 0.8,
      ease: Power0.easeNone
    })
    gsap.delayedCall(0.5, () => {
      if (this.mQuizSound[this.mGameStep])
        this.mQuizSound[this.mGameStep].play()
    })
    // this.mDeepMotion = gsap.timeline({ repeat: 5, yoyo: true })
    // this.mDeepMotion.to(this.mDeepAry[this.mGameStep], {
    //   alpha: 1,
    //   duration: 0.25
    // })

    // this.mDeepMotion.eventCallback('onComplete', () => {
    //   gsap.delayedCall(3, () => {
    //     if (this.mFocusAry[this.mGameStep]) this.focus()
    //   })
    // })
    if (this.mFocusAry[this.mGameStep]) {
      // this.mFocusAry[this.mGameStep].alpha = 1
      this.mDeepAry[this.mGameStep].alpha = 1
    }
  }

  completeFocus() {
    // if (this.mDeepMotion) {
    //   this.mDeepMotion.kill()
    //   this.mDeepMotion = null
    // }
    if (this.mFocusMotion) {
      this.mFocusMotion.kill()
      this.mFocusMotion = null
    }

    this.mFocusAry[this.mGameStep].alpha = 0
    gsap.to(this.mDeepAry[this.mGameStep], { alpha: 1, duration: 1 })
    gsap.to(this.mTextAry[this.mGameStep], { alpha: 1, duration: 1 })
    gsap.to(this.mPinkAry[this.mGameStep], { alpha: 1, duration: 1 })
  }

  stopFocus() {
    if (this.mDeepMotion) {
      this.mDeepMotion.kill()
      this.mDeepMotion = null
    }
    if (this.mFocusMotion) {
      this.mFocusMotion.kill()
      this.mFocusMotion = null
    }
    for (const deep of this.mDeepAry) {
      deep.alpha = 1
    }
    for (const focus of this.mDeepAry) {
      focus.alpha = 0
    }
  }

  quizStepBlink() {
    for (let i = 0; i < this.mTextAry.length; i++) {
      this.mTextAry[i].blink(false)
      if (this.mGameStep == i) {
        this.mTextAry[i].blink(true)
      }
    }
  }

  repeatBlink() {
    for (const text of this.mTextAry) {
      text.blink(true)
    }
  }

  // disable(flag: boolean) {
  //   if (flag) {
  //     for (let i = 0; i < this.mDeepAry.length; i++) {
  //       this.mDeepAry[i].texture = ResourceManager.Handle.getViewerResource(
  //         `type${this.mInfo[i].type}_${i + 1}deep.png`
  //       ).texture
  //     }
  //   } else {
  //     for (let i = 0; i < this.mDeepAry.length; i++) {
  //       this.mDeepAry[i].texture = ResourceManager.Handle.getViewerResource(
  //         `box_type${this.mInfo[i].type}_${i + 1}ex_dim.png`
  //       ).texture
  //     }
  //   }
  // }
}

export class TypeBody extends PIXI.Container {
  private mAffordance: EventSprite
  private mAffordanceMotion: any
  private mWord: string

  /**ex ''
   * => mSyllableStep=1
   * => mSyllableStep=2
   * */
  private mSyllableStep: number

  /**ex ''
   * => mSyllableGameStep=1
   * => mSyllableGameStep=2
   * => mSyllableGameStep=3
   * */
  private mSyllableGameStep: number

  private mGameStep: number
  set gameStep(v: number) {
    this.mGameStep = v
    this.mSlicedSyllable[this.mSyllableStep].gameStep = v
  }

  /**
   * =>Type1
   * =>Type2
   * =>Type3
   * =>Type4
   * =>Type5
   * =>Type6
   * */
  private mGameData: object
  get gameData(): object {
    return this.mGameData
  }

  get currentSound(): HTMLAudioElement {
    return this.mSlicedSyllable[this.mSyllableStep].firstSound
  }
  private mSlicedSyllable: Array<SlicedSyllable>

  constructor(word: string) {
    super()
    this.mSyllableStep = 0
    this.mGameStep = 0
    this.mWord = word
    this.mSlicedSyllable = []

    this.decideType()
    this.createText()

    this.mSyllableGameStep = 0
  }

  /**   */
  decideType() {
    // '',     
    //  * ex=>
    // this.mGameData = {
    //   0: {
    //     type: 1,
    //     text: '',
    //     index: 0,
    //     syllableIndex: 0,
    //     syllableType: '1cho'
    //   },
    //   1: {
    //     type: 1,
    //     text: '',
    //     index: 0,
    //     syllableIndex: 1,
    //     syllableType: '2jung'
    //   },
    //   3: {
    //     type: 4,
    //     text: '',
    //     index: 1,
    //     syllableIndex: 0,
    //     syllableType: '1cho'
    //   },
    //   4: {
    //     type: 4,
    //     text: '',
    //     index: 1,
    //     syllableIndex: 1,
    //     syllableType: '2jung'
    //   },
    //   5: {
    //     type: 4,
    //     text: '',
    //     index: 1,
    //     syllableIndex: 2,
    //     syllableType: '3jong'
    //   }
    // }
    let step = 0
    this.mGameData = {}
    for (let i = 0; i < this.mWord.length; i++) {
      const syllable = getConstantVowel(this.mWord[i])
      const type = getCombinationType(this.mWord[i])

      for (let j = 0; j < syllable.length; j++) {
        let syllabletype = ''
        if (j == 0) {
          syllabletype = `1cho_t${type}`
        }
        if (j == 1) {
          syllabletype = `2jung_t${type}`
        }
        if (j == 2) {
          syllabletype = '3jong_at'
        }
        const ary = {
          type: type,
          text: syllable[j],
          index: i,
          syllableIndex: j,
          syllableType: syllabletype
        }
        if (i == 0) {
          this.mGameData[j] = ary
          step = j
        }
        if (i == 1) {
          this.mGameData[j + syllable.length - 1] = ary
          step = j + syllable.length
        }
        if (i == 2) {
          this.mGameData[j + step] = ary
        }
      }
    }

    for (let i = 0; i < this.mWord.length; i++) {
      console.log(
        `%c ${this.mWord[i]}  "${getCombinationType(
          this.mWord[i]
        )}".`,
        'border:2px red solid;letter-spacing:-0.1rem;font-weight:bold;'
      )
    }
  }

  /**   */
  createText() {
    /*
     * ex -> 
     * slicedAray1 ['','']
     * slicedAray2 ['','']
     * slicedAray3 ['','']
     */
    const slicedAray1 = []
    const slicedAray2 = []
    const slicedAray3 = []
    let bodyCount = 0
    for (let i = 0; i < Object.keys(this.mGameData).length; i++) {
      if (this.mGameData[i].index == 0) {
        slicedAray1.push(this.mGameData[i])
        bodyCount = 1
      }
      if (this.mGameData[i].index == 1) {
        slicedAray2.push(this.mGameData[i])
        bodyCount = 2
      }
      if (this.mGameData[i].index == 2) {
        slicedAray3.push(this.mGameData[i])
        bodyCount = 3
      }
    }
    const slicedAray = [slicedAray1, slicedAray2, slicedAray3]

    let offsetX = 280 / 2
    for (let i = 0; i < bodyCount; i++) {
      const sliced = new SlicedSyllable(slicedAray[i])
      this.addChild(sliced)
      sliced.position.set(offsetX, 280 / 2)
      offsetX += sliced.width + 20
      this.mSlicedSyllable.push(sliced)
    }
    this.pivot.set(this.width / 2, this.height / 2)
  }

  /**      */
  getStep(end?: boolean) {
    /**      
     * ex > 
     *  => gamestep=1 //  => gamestep=2  //  => gamestep=3
     *  => syllableStep=1,gamestep=1
     *  => syllableStep=2,gamestep=2
     *  => syllableStep=1,gamestep=3
     *   
     */
    const syllableTotalStep = this.mWord.length
    const syllable = {}
    for (let i = 0; i < syllableTotalStep; i++) {
      syllable[i] = getConstantVowel(this.mWord[i]).length
      if (i >= 1) {
        syllable[i] = getConstantVowel(this.mWord[i] + this.mWord[i - 1]).length
      }
    }

    const syllabletotal = this.mSlicedSyllable[this.mSyllableStep].totalStep

    if (this.mSyllableGameStep >= syllabletotal) {
      this.mSyllableStep += 1
    }
    if (this.mGameStep <= syllable[this.mSyllableGameStep]) {
      this.mSyllableGameStep += 1
    } else {
      this.mSyllableGameStep = 0
    }
    console.log(
      `%c     ${this.mSyllableGameStep}`,
      'border:1px red solid;'
    )
    console.log(
      `%c      ${
        this.mSlicedSyllable[this.mSyllableStep].totalStep
      }`,
      'border: 1px green solid;'
    )
    console.log(
      `%c  ${this.mSyllableStep + 1}  `,
      'border: 1px blue solid'
    )

    this.mSlicedSyllable[this.mSyllableStep].focus()

    if (end) {
      for (const slice of this.mSlicedSyllable) {
        slice.stopFocus()
      }
    }
  }

  //   
  introVersion() {
    //  

    // let sound = document.createElement('audio')
    // sound.src = `${config.restAPI}common/sounds/word_${config.studyDay}.mp3`
    // sound.play()
    // sound.onloadedmetadata = () => {
    //   gsap.delayedCall(sound.duration, () => {
    //     sound.pause()
    //     sound = null
    //   })
    // }

    for (const sliced of this.mSlicedSyllable) {
      sliced.introVersion = true
    }
    this.mAffordance = new EventSprite(
      ResourceManager.Handle.getCommonResource(`hand.png`).texture
    )
    this.mAffordance.position.set(this.width / 2, this.height / 2 + 50)
    this.mAffordance.alpha = 0
    this.addChild(this.mAffordance)
    this.mAffordance.zIndex = 4

    this.mAffordanceMotion = gsap.timeline({ repeat: -1 })
    this.mAffordanceMotion
      .to(this.mAffordance, {
        alpha: 1,
        duration: 0.25,
        ease: Power0.easeNone
      })
      .to(this.mAffordance, {
        angle: -10,
        duration: 0.25
      })
      .to(this.mAffordance, {
        angle: 10,
        duration: 0.25
      })
      .to(this.mAffordance, {
        alpha: 0,
        duration: 0.25,
        ease: Power0.easeNone
      })
      .to(this.mAffordance, {
        angle: 0,
        duration: 1
      })

    this.interactive = true
    this.buttonMode = true
    gsap
      .to(this.scale, { x: 0.95, y: 0.95, duration: 0.5 })
      .repeat(-1)
      .yoyo(true)

    this.hitArea = new Rectangle(0, 0, this.width, this.height)

    this.on('pointertap', () => {
      this.interactive = false
      this.buttonMode = false
      gsap.killTweensOf(this.scale)
      if (this.mAffordanceMotion) this.mAffordanceMotion.kill()
      gsap.killTweensOf(this.mAffordance)
      this.removeChild(this.mAffordance)
      this.mAffordance = null
      console.log(` () `)
      this.goGameScene()
    })
  }

  goGameScene() {
    for (const sliced of this.mSlicedSyllable) {
      sliced.repeatBlink = () => null
    }
    this.mAffordance = null

    this.mSlicedSyllable = []

    gsap
      .to(this.scale, { x: 1, y: 1, duration: 0.5 })
      .eventCallback('onComplete', () => {
        gsap.delayedCall(0.5, () => {
          soundPlay(`word_${config.studyDay}`, true)
          gsap.delayedCall(1.5, () => {
            this.removeChildren()
            ;(this.parent as IntroScene).next()
          })
        })
      })
  }
}

export class SideBarSliced extends PIXI.Container {
  private mBg: PIXI.Sprite
  private mTextSprite: PIXI.Sprite
  private mCorrectFlag: boolean
  get correctFlag(): boolean {
    return this.mCorrectFlag
  }

  private mGameData: any

  constructor(
    bg: string,
    textSprite: string,
    correctFlag: boolean,
    gameData: any
  ) {
    super()
    this.mGameData = gameData
    this.mCorrectFlag = correctFlag
    /**bg = pink */
    this.mBg = new PIXI.Sprite(
      ResourceManager.Handle.getCommonResource(bg).texture
    )
    this.mTextSprite = new PIXI.Sprite(
      ResourceManager.Handle.getCommonResource(textSprite).texture
    )
    this.mBg.position.set(this.width / 2, this.height / 2)
    this.addChild(this.mBg)

    this.mTextSprite.tint = 0x000000
    this.mTextSprite.anchor.set(0.5)
    this.mTextSprite.position.set(
      this.mTextSprite.width / 2,
      this.mTextSprite.height / 2
    )
    this.addChild(this.mTextSprite)

    this.pivot.set(this.width / 2, this.height / 2)

    this.interactive = true
    this.buttonMode = true

    this.on('pointerup', (evt: PIXI.InteractionEvent) => {
      const clickColor = getColorByPoint(
        this.mBg,
        new PIXI.Point(evt.data.global.x, evt.data.global.y)
      ).a
      if (clickColor != 0) {
        this.onClick()
      }
    })
  }
  onClick() {
    /** */
  }

  disable() {
    this.mBg.texture = ResourceManager.Handle.getCommonResource(
      `side_type${this.mGameData.type}_dim${this.mGameData.syllableIndex +
        1}.png`
    ).texture
    this.mTextSprite.alpha = 0.5
  }
}

export class SideBar extends PIXI.Sprite {
  private mAffordance: Affordance
  private mGameData: object
  private mGameStep: number
  set gameStep(v: number) {
    this.mGameStep = v
    this.createEx()
  }
  private mCorrect: SideBarSliced
  private mWrong: SideBarSliced
  constructor(gameData: object) {
    super()
    this.texture = ResourceManager.Handle.getViewerResource(
      'side_bar.png'
    ).texture

    this.mGameStep = 0
    this.mGameData = gameData

    this.interactive = true
    this.on('pointerdown', () => {
      console.log(`resetTimer`)
      if (this.mAffordance) this.mAffordance.resetTimer()
    })
  }

  //   
  async createEx() {
    if (this.mAffordance) {
      console.log(this.mAffordance)
      await this.mAffordance.reset()
      this.mAffordance = null
    }
    // for (let i = 0; i < this.children.length; i++) {
    //   this.children[i] = null
    // }
    this.removeChildren()

    const gameData = this.mGameData[this.mGameStep]
    /**
     * ex gameData=
     *    {
     *        index: 0
     *        text: ""
     *        type: 1,
     *        syllableIndex: 0
     *        syllableType: '1cho'
     *    }
     */
    let slicedAry = []

    this.mCorrect = new SideBarSliced(
      `side_type${gameData.type}_pink${gameData.syllableIndex + 1}.png`,
      `${gameData.syllableType}_${syllableToNum(gameData.text)}.png`,
      true,
      gameData
    )
    if (gameData.syllableIndex == 0) {
      this.mCorrect.x = 320 / 2 + this.mCorrect.width / 4
    }
    if (gameData.syllableIndex == 1) {
      this.mCorrect.x = 320 / 2 - this.mCorrect.width / 4
    }
    this.mCorrect.y = config.h / 2

    this.mWrong = new SideBarSliced(
      `side_type${gameData.type}_pink${gameData.syllableIndex + 1}.png`,
      `${gameData.syllableType}_${syllableToNum(
        config.partnerWrong[this.mGameStep]
      )}.png`,
      false,
      gameData
    )

    this.mWrong.y = config.h / 2

    slicedAry = [this.mCorrect, this.mWrong]

    const sliced = shuffleArray(slicedAry)
    for (let i = 0; i < sliced.length; i++) {
      sliced[i].x = 290 / 2 + 28
      sliced[i].y = config.h / 2

      const pos =
        config.position[`type${gameData.type}`][`${gameData.syllableIndex}`]
      // gsap.to(sliced[i], {
      //   x: sliced[i].x + pos.x,
      //   y: sliced[i].y + pos.y,
      //   duration: 0.5
      // })
      sliced[i].x += pos.x
      sliced[i].y += pos.y

      console.log(
        `%c ${gameData.type}: ${gameData.syllableIndex + 1} , pos:x=>[${
          pos.x
        },y=>${pos.y} ]`,
        'border:2px red solid;'
      )

      this.addChild(sliced[i])

      let moveY = 0
      if (i == 0) {
        moveY = config.h / 2 - 150
      } else {
        moveY = config.h / 2 + 150
      }
      gsap.to(sliced[i], { y: moveY, duration: 0.5 })
    }

    this.registerCheck()
  }

  //       
  async registerCheck() {
    this.mAffordance = new Affordance(this, true)
    this.addChild(this.mAffordance)

    this.mCorrect.onClick = () => {
      this.mCorrect.onClick = () => null
      this.wrongMotion()

      console.log(``)
      soundPlay(`ac4_correct`)
      gsap.delayedCall(1, () => {
        ;(this.parent as GameScene).next()
      })
    }
    this.mWrong.onClick = () => {
      this.mWrong.onClick = () => {
        soundPlay(`ac4_wrong`)
      }
      soundPlay(`ac4_wrong`)
      this.wrongMotion()
      console.log(``)
    }
  }

  wrongMotion() {
    this.mWrong.disable()
  }
}

export class IntroScene extends PIXI.Container {
  private mSideBar: PIXI.Sprite
  constructor() {
    super()
  }
  onStart() {
    this.createBg()
    this.createText()
  }

  createBg() {
    const bg = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource('bg.png').texture
    )
    this.addChild(bg)
    for (let i = 0; i < 2; i++) {
      const bgcharacter = new BgCharacter(i)
      if (i == 0) {
        bgcharacter.position.set(80, 500)
        bgcharacter.move(500)
      } else {
        bgcharacter.position.set(762, 170)
        bgcharacter.move(170)
      }
      bg.addChild(bgcharacter)
    }

    this.mSideBar = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource('intro_side_bar.png').texture
    )
    this.mSideBar.position.set(config.w, 0)

    bg.addChild(this.mSideBar)
    gsap.to(this.mSideBar, { x: config.w - this.mSideBar.width, duration: 1 })
  }

  createText() {
    const typeBody = new TypeBody(config.quizWord)
    typeBody.position.set(config.w / 2 - this.mSideBar.width / 2, config.h / 2)
    this.addChild(typeBody)
    typeBody.introVersion()
  }

  next() {
    ;(this.parent as Partner).setGame()
  }
}

export class GameScene extends PIXI.Container {
  private mSideBar: SideBar
  private mTypeBody: TypeBody

  private mGameData: object
  private mGameStep: number
  get gameStep(): number {
    return this.mGameStep
  }
  set gameStep(v: number) {
    if (v >= Object.keys(this.mGameData).length) {
      this.mTypeBody.gameStep = v
      this.mTypeBody.getStep(true)
      gsap.delayedCall(1, () => {
        console.log(` !`)
        this.outro()
      })
    } else {
      this.mGameStep = v
      this.mTypeBody.gameStep = v
      this.mSideBar.gameStep = this.mGameStep

      this.mTypeBody.getStep()
    }
  }

  constructor() {
    super()
  }
  onStart() {
    // this.outro()
    this.createBg()
    this.createText()
  }

  /**  ,   */
  createBg() {
    const bg = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource('bg.png').texture
    )
    this.addChild(bg)
    for (let i = 0; i < 2; i++) {
      const bgcharacter = new BgCharacter(i)
      if (i == 0) {
        bgcharacter.position.set(80, 500)
        bgcharacter.move(500)
      } else {
        bgcharacter.position.set(762, 170)
        bgcharacter.move(170)
      }
      bg.addChild(bgcharacter)
    }

    //  
    // const wordNum = getSound(config.quizWord)
    const wordNum = getSound('')
    const sndBtn = new SoundBtn(`word_${wordNum}.mp3`)
    sndBtn.position.set(config.w / 2 - 320 / 2, 150)
    this.addChild(sndBtn)
  }

  //    
  createText() {
    this.mGameStep = 0

    this.mTypeBody = new TypeBody(config.quizWord)
    this.mTypeBody.position.set(config.w / 2 - 320 / 2, config.h / 2)
    this.addChild(this.mTypeBody)
    this.mTypeBody.getStep()

    this.mGameData = this.mTypeBody.gameData

    this.mSideBar = new SideBar(this.mGameData)
    this.mSideBar.position.set(config.w - this.mSideBar.width, 0)
    this.addChild(this.mSideBar)

    this.mSideBar.createEx()
    gsap.delayedCall(0.5, () => {
      this.mTypeBody.currentSound.play()
    })
  }

  next() {
    this.gameStep += 1
  }

  async outro() {
    soundPlay('ac4_cardpop')
    this.mTypeBody.getStep(true)
    const dimmed = new Dimmed(config.w, config.h)
    this.addChild(dimmed)
    const card = new PIXI.Sprite(
      ResourceManager.Handle.getViewerResource(`card.png`).texture
    )
    card.anchor.set(0.5)
    card.position.set(config.w / 2, config.h / 2)
    card.scale.set(0)
    this.addChild(card)

    const complete = new EventSprite(
      ResourceManager.Handle.getCommonResource(
        `word_${config.studyDay}.png`
      ).texture
    )
    complete.position.set(-260, 0)

    const text = new PIXI.Text(config.quizWord, {
      fontSize: 200,
      padding: 20,
      fontFamily: 'TmoneyRoundWindExtraBold'
    })
    text.anchor.set(0.5)
    text.position.set(230, 0)

    card.addChild(complete, text)

    gsap.to(card.scale, { x: 1, y: 1, duration: 0.5 })
    gsap.delayedCall(2, () => {
      soundPlay(`word_${config.studyDay}`, true)
      gsap.delayedCall(2, () => {
        ;(this.parent as Partner).endgame()
      })
    })
  }
}

export class Partner extends SceneBase {
  private mBGM: HTMLAudioElement
  private mIntroScene: IntroScene
  private mGameScene: GameScene

  constructor() {
    super('partner')
  }
  async onInit() {
    await App.Handle.showLoadingScreen()

    await ResourceManager.Handle.loadCommonResource(
      PartnerProduct[`day${config.studyDay}`]
    )
    await ResourceManager.Handle.loadCommonResource({
      images: config.partnerNtotal
    })
    await ResourceManager.Handle.loadViewerResource(PartnerResource)

    await App.Handle.closeLoadingScreen()
  }
  async onStart() {
    let intro = new Intro()
    this.addChild(intro)
    await intro.intro()
    this.removeChild(intro)
    intro = null
    this.mBGM = document.createElement('audio')
    this.mBGM.src = `${api}ac4_bgm.mp3`
    this.mBGM.play()
    this.mBGM.loop = true
    window['currentBGM'] = this.mBGM
    this.mIntroScene = new IntroScene()
    this.addChild(this.mIntroScene)
    this.mIntroScene.onStart()

    // this.setGame()
  }

  setGame() {
    this.removeChildren()
    this.mIntroScene = null
    this.mGameScene = new GameScene()
    this.addChild(this.mGameScene)
    this.mGameScene.onStart()
  }

  async endgame() {
    let eop = new Eop()
    this.addChild(eop)
    await eop.start()
    eop = null
    this.mBGM.pause()
    this.mBGM = null
    window['currentBGM'].pause()
    window['currentBGM'] = null
    this.removeChildren()
    App.Handle.nextGame()
  }
}
