import gsap from 'gsap'
import { App } from '@/activity/core/app'
import { ResourceManager } from '@/activity/core/resourceManager'
import config from '@/activity/gameUtil/config'
import { soundPlay } from '../gameUtil/game'

export class Intro extends PIXI.Container {
  constructor() {
    super()
  }
  intro(): Promise<void> {
    return new Promise<void>((resolve) => {
      App.Handle.modalRoot.visible = false

      console.log(
        `%c      `,
        'background: black;color:white'
      )
      // soundPlay(`com_intro_sfx`, true)

      const intro = new PIXI.spine.Spine(
        ResourceManager.Handle.getCommonResource(`intro.json`).spineData
      )
      this.addChild(intro)
      intro.position.set(config.w / 2, config.h / 2)
      intro.state.setAnimation(0, `0${App.Handle.currentSceneIDX}`, false)

      const delay = intro.state.data.skeletonData.findAnimation(
        `0${App.Handle.currentSceneIDX}`
      ).duration

      gsap.delayedCall(delay, () => {
        App.Handle.modalRoot.visible = true
        resolve()
      })

      gsap.delayedCall(2.3, () => {
        soundPlay(`ac${App.Handle.currentSceneIDX}_title`, true)
      })
    })
  }
}
