export function syllableToNum(syllable: string) {
  let syllableCode = ''
  const list = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
  for (const num of list) {
    if (num.toString() == syllable) {
      syllableCode = (list.indexOf(num) + 1).toString()
      break
    }
  }

  return syllableCode
}

