export function getSyllableType(syllable: string) {
  let type = ''
  const cho = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
  const jung = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
  const jong = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
  for (const chosung of cho) {
    if (syllable == chosung) {
      type = '1cho_'
      break
    }
  }
  for (const jungsung of jung) {
    if (syllable == jungsung) {
      type = '2jung_'
      break
    }
  }
  for (const jongsung of jong) {
    if (syllable == jongsung) {
      type = '3jong_'
      break
    }
  }

  return type
}
